<template>

<div>
        <el-table
        :data="ShowTimeData"
        style="width: 100%">

            <el-table-column
                prop="sections"
                label=""
                width="100">
            </el-table-column>

            <el-table-column
                prop="startTime"
                label=""
                width="200">
            </el-table-column>

            <el-table-column
                prop="lastTime"
                label=""
                width="200">
            </el-table-column>

            <el-table-column
                prop="lateTime"
                label=""
                width="200">
            </el-table-column>

            <el-table-column
                label=""
                width="200">

                <template slot-scope="scope">
                <el-button
                size="mini"
                type="primary"
                @click="handleEdit(scope.row)"></el-button>
                </template>
                
            </el-table-column>
            
        </el-table>

        <el-table
        :data="ShowTimeWeek"
        style="width: 100%">
            <el-table-column
                prop="week"
                label=""
                width="200">
            </el-table-column>

            <el-table-column
                prop="originalTime"
                label=""
                width="200">
            </el-table-column>

            <el-table-column
                prop="changeTime"
                label=""
                width="200">
            </el-table-column>

            <el-table-column
                label=""
                width="300">
                <template slot-scope="scope">
                <el-button
                size="mini"
                type="primary"
                @click="handleWeekEdit(scope.row)"></el-button>
                <el-button
                size="mini"
                type="primary"
                :disabled="buttonClicked"
                @click="handleDateEdit()"></el-button>
                </template>
            </el-table-column>
            
        </el-table>


         <el-dialog title="" :visible.sync="updateTimeForm" width="500px" >

            <el-form :model="OneTimeDate"  ref="OneTimeDate" label-width="100px" class="demo-ruleForm">
                
                <div>
                    <span style="font-size:20px">:{{this.OneTimeDate.sections}}</span>   
                </div>

                <el-form-item label="">
                    <el-input v-model="OneTimeDate.startTime" ></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="OneTimeDate.lastTime" ></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="OneTimeDate.lateTime" ></el-input>
                </el-form-item>

                <el-row>
                    <el-button type="primary" @click="updateTime()"></el-button>
                    <el-button type="primary" @click="close()"></el-button>
                </el-row>
            </el-form>
              <span style="color:red;margin-top:30px">!:00:00:00</span>
        </el-dialog>



        <el-dialog title="" :visible.sync="updateWeek" width="300px" >
            <el-form :model="upweekInf"  ref="OneTimeDate" label-width="100px" class="demo-ruleForm">

                <el-form-item label="">
                    <el-input v-model.number="upweekInf.week" ></el-input>
                </el-form-item>

                <el-row>
                    <el-button type="primary" @click="changeWeek()"></el-button>
                    <el-button type="primary" @click="closeWeek()"></el-button>
                </el-row>
            </el-form>
        </el-dialog>

</div>
  
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            ShowTimeData:[],
            updateTimeForm:false,
            ShowTimeWeek:[],
            OneTimeDate:{
                sections:'',
                startTime:'',
                lastTime:'',
                lateTime:'',
            },
            upweekInf:{},
            buttonClicked:false,
            updateWeek:false
        }
    },
    mounted(){
        this.getSchedule(),
        this.getWeek()
    },
    methods:{
        //
        getSchedule(){
            axios.get("/schedule/getSchedule").then((res)=>{
                this.ShowTimeData = res.data.data
            })
        },
        //
        getWeek(){
            axios.get("/schedule/getWeek").then((res)=>{
                this.ShowTimeWeek = res.data.data;
            })
        },

        //
        getOneTime(sections){
            axios.get("/schedule/getOneTime/"+sections).then((res)=>{
                this.OneTimeDate = res.data.data
            })
        },
        //
        handleEdit(row){
            this.getOneTime(row.sections)
            this.updateTimeForm = true
        },
        //
        updateTime(){
            console.log(this.OneTimeDate);
            axios.put("/schedule/updateTime",this.OneTimeDate).then((res)=>{
                if(res.data.code === 202381){
                    this.getSchedule()
                    this.$message.success(res.data.data)
                    this.updateTimeForm = false
                }else if(res.data.code === 202380){
                    this.$message.error("!")
                }else{
                    this.$message.error(res.data.msg)
                }
            })
        },
        //
        handleWeekEdit(row){
            this.updateWeek = true;
            axios.get("/schedule/getWeek").then((res)=>{
                this.upweekInf = res.data.data[0];
            })
        },
        //
        handleDateEdit(){
            this.buttonClicked = true;
            axios.put("/schedule/updateOriginalTime").then((res)=>{
                this.$message.success(res.data.data)
                this.getWeek()
            })
        },
        //
        changeWeek(){
            axios.put("/schedule/updateWeek/"+this.upweekInf.week).then((res)=>{
                if(res.data.code === 202381){
                    this.$message.success(res.data.data)
                    this.updateWeek = false
                    this.getWeek()
                    this.upweekInf = {}
                }else if(res.data.code === 202380){
                    this.$message.error(res.data.data)
                }else{
                    this.$message.error(res.data.msg)
                }
            })
        },
        close(){
            this.updateTimeForm = false
            this.OneTimeDate = {}
        },
        closeWeek(){
            this.updateWeek = false
            this.upweekInf = {}
        },
        closeUpdateTable(){
            this.updateTimeForm = false
        }
    }
}
</script>

<style>

</style>