<template>
  <div>
    <div style="margin-top:30px">
        <el-upload
          class="upload-demo"
          ref="upload"
          action=""
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList"
          :limit="1"
          :on-exceed="handleExceed"
          :auto-upload="false">

            <el-button slot="trigger" size="small" type="primary" @click="select"></el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload" :disabled="buttonClicked"></el-button>
          <div slot="tip" class="el-upload__tip">
            jpg/png500kb 
            <span style="color:red"></span>  
          </div>
        </el-upload>
        <div v-if="this.imgUrl">
          <p style="font-size:20px"></p>
          <img v-if="this.imgUrl" :src="imgUrl" style="width:1000px;height:500px">
        </div>
          


        <!--  -->
        <div v-if="this.ShowStuInformationData.length !== 0 && this.imgUrl !== '' " style="margin-top:15px">
          <span style="font-size:20px"></span>
          <el-table
          :data="ShowStuInformationData"
          style="width: 100%">

            <el-table-column
                  prop="stuName"
                  label=""
                  width="100">
              </el-table-column>

              <el-table-column
                  prop="stuCode"
                  label=""
                  width="150">
              </el-table-column>

              <el-table-column
                  prop="classToClass"
                  label="/"
                  width="100">
              </el-table-column>

              <el-table-column
                  prop="monday"
                  label=""
                  width="120">
              </el-table-column>

              <el-table-column
                  prop="tuesday"
                  label=""
                  width="120">
              </el-table-column>

              <el-table-column
                  prop="wednesday"
                  label=""
                  width="120">
              </el-table-column>

              <el-table-column
                  prop="thursday"
                  label=""
                  width="120">
              </el-table-column>

              <el-table-column
                  prop="friday"
                  label=""
                  width="120">
              </el-table-column>

              <el-table-column
                  label=""
                  width="120">
                  <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" circle @click="changeTable(scope.row)"></el-button>
                  </template>
              </el-table-column>
          </el-table>
          <el-button type="primary" round @click="submitTable"></el-button>
        </div>
    </div>

    <el-dialog title="" :visible.sync="FormUpdateStu">
      <el-form  :model="ChangeStuData" label-position="right"
                  label-width="78px" >

          <el-row>
              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.stuName"/>
                  </el-form-item>
              </el-col>

              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.stuCode"/>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.monday"/>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.tuesday"/>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.wednesday"/>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.thursday"/>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="">
                      <el-input v-model="ChangeStuData.friday"/>
                  </el-form-item>
              </el-col>
          </el-row>
          <el-button type="primary" round @click="submitInf"></el-button>
      </el-form>
      <div>
        <span style="color:red;font-size:20px">,://(0-0)////</span>
      </div>
    </el-dialog>

  </div>
</template>


<script>
import axios from 'axios';

  export default {
    data() {
      return {
        fileList: [],
        imgUrl:"",
        name:"",
        ShowStuInformationData: [],
        ChangeStuData:{},
        FormUpdateStu:false,
        buttonClicked:false
      };
    },
    methods: {
      submitUpload() {  
        if(this.imgUrl !== ""){
            this.buttonClicked = true
            axios.post("/addStu/"+this.name+"g").then((res)=>{
              if(res.data.code === 202350){
                this.$message.warning(res.data.data)
                this.buttonClicked = false
              }else{
                this.$message.warning("!")
                this.ShowStuInformationData = res.data.data
                console.log(this.ShowStuInformationData);
              }
          })
         
        }else{
          this.$message.warning("")
        }
      },
      select(){
        this.buttonClicked = false
      },
      handleRemove(file, fileList) {
        this.imgUrl = ""
        this.ShowStuInformationData = []
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
        this.name = file.name;
        this.imgUrl = URL.createObjectURL(file.raw);
      },

      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },

      //
      changeTable(row){
        console.log(row),
        this.FormUpdateStu = true,
        axios.post("/addStu/change",row).then((res)=>{
            this.ChangeStuData = res.data.data
        })
      },
      //
      submitInf(){
        if(this.ChangeStuData.monday.length <= 7 || this.ChangeStuData.tuesday.length <= 7 ||this.ChangeStuData.wednesday.length <= 7||this.ChangeStuData.thursday.length <= 7||this.ChangeStuData.friday.length <= 7){
          this.$message.warning("!")
        }else{
          axios.put("/addStu/update",this.ChangeStuData).then((res)=>{
            if(res.data.code === 202381){
              this.ShowStuInformationData = res.data.data
              this.$message.success("!")
              this.FormUpdateStu = false
            }else if(res.data.code === 202380){
              this.$message.warning(res.data.data)
            }else{
              this.$message.warning("!")
            }
          })
        }

      },
      
      //
      submitTable(){
        axios.post("/addStu",this.ShowStuInformationData).then((res)=>{
          if(res.data.code === 202351){
              this.$message.success(res.data.data)
              this.imgUrl = ""
              this.buttonClicked = false
          }else if(res.data.code === 202350){
            this.$message.error(res.data.data)
            this.imgUrl = ""
            this.buttonClicked = false
          }else{
              this.$message.error("!")
          }
          
        })
      }

    }
  }
</script>

