<template>
  <div>
      <div style="margin-top:30px">
            <el-input
            style="width:300px"
            v-model="search"
            size="mini"
            placeholder=""/>
        </div>
        <div style="margin-top:10px">
            <el-button type="primary" @click="addAdminForm"></el-button>
        </div>
          <el-table
            :data="adminDate.filter(data => !search || data.stuName.toLowerCase().includes(search.toLowerCase()))"
            style="width: 1000px">
            
            <el-table-column
            prop="id"
            label="ID"
            width="150"
            >
            </el-table-column>

            <el-table-column
            prop="stuName"
            label=""
            width="180"
            >
            </el-table-column>

            <el-table-column
            label=""
            prop="stuCode">
            </el-table-column>

            <el-table-column
            label=""
            prop="createTime">
            </el-table-column>

            <el-table-column
            label=""
            prop="updateTime">
            </el-table-column>

            <el-table-column
            align="right"
            label="">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.row)"></el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="" :visible.sync="dialogFormVisible4UpdateAdmin" width="800px" >

            <el-form :model="UpdateData"  ref="UpdateData" label-width="100px" class="demo-ruleForm">

                <span style="font-size:20px">ID:{{this.UpdateData.id}}</span>     

                <el-form-item  prop="stuName" label="">
                    <el-input v-model="UpdateData.stuName" placeholder=""></el-input>
                </el-form-item>

                <el-form-item  prop="stuCode" label="">
                    <el-input v-model="UpdateData.stuCode" placeholder=""></el-input>
                </el-form-item>

            </el-form>

            <el-row>
                    <el-button type="primary" @click="Update('UpdateData')"></el-button>
                    <el-button type="primary" @click="close()"></el-button>
            </el-row>

        </el-dialog>    

        <el-dialog title="" :visible.sync="dialogFormVisibleAddAdmin" width="800px" >

            <el-form :model="AddAdminData"  ref="UpdateData" label-width="100px" class="demo-ruleForm">

                <el-form-item  prop="stuName" label="">
                    <el-input v-model="AddAdminData.stuName" placeholder=""></el-input>
                </el-form-item>

                <el-form-item  prop="stuCode" label="">
                    <el-input v-model="AddAdminData.stuCode" placeholder=""></el-input>
                </el-form-item>

            </el-form>

            <el-row>
                    <el-button type="primary" @click="Add('AddAdminData')"></el-button>
                    <el-button type="primary" @click="close()"></el-button>
            </el-row>

        </el-dialog> 

  </div>
</template>

<script>
import axios from 'axios';
export default {



    data() {
        return {
            adminDate:[],
            UpdateData:{},
            AddAdminData:{},
            dialogFormVisible4UpdateAdmin:false,
            dialogFormVisibleAddAdmin:false,
            search:'',
        
        }
    },
    mounted(){
        this.getAllAdmin();
    },
    methods:{

   
        getAllAdmin(){
            axios.get("/admin").then((res)=>{
                this.adminDate = res.data.data
            })
        },

        getOneAdmin(id){
            axios.get("/admin/"+id).then((res)=>{
                this.UpdateData = res.data.data
            })
        },

        Update(UpdateData){
            axios.put("/admin",this.UpdateData).then((res)=>{
                if(res.data.code === 202381){
                    this.$message.success(res.data.data)
                    this.getAllAdmin()
                    this.dialogFormVisible4UpdateAdmin = false
                }else if(res.data.code === 202380){
                    this.$message.error("!")
                }else if(res.data.code === 0){
                    this.$message.warning(res.data.data);
                }
            })
        },

        handleEdit(row){
            this.getOneAdmin(row.id)
            this.dialogFormVisible4UpdateAdmin =true
        },

        handleDelete(row){
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios.delete("/admin/"+row.id).then((res)=>{
                    this.$message.success(res.data.data)
                    this.getAllAdmin()
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            })
        },
        addAdminForm(){
            this.dialogFormVisibleAddAdmin = true
        },

        Add(addInf){
            if(this.AddAdminData.stuCode.length >= 13){
                this.$message.warning("13")
            }else{
                axios.post("/admin",this.AddAdminData).then((res)=>{
                    if(res.data.code === 202351){
                        this.dialogFormVisibleAddAdmin = false
                        this.getAllAdmin()
                        this.AddAdminData = {}
                    }else if(res.data.code === 202350){
                        this.$message.error(res.data.data)
                    }else{
                        this.$message.warning("")
                    }
                })
            }
        },

        close(){
            this.dialogFormVisible4UpdateAdmin = false
        }
    }
}
</script>

