<template>
    <div>
        <div style="margin-top:30px">
            <el-input
            style="width:300px"
            v-model="search"
            size="mini"
            placeholder=""/>
        </div>

          <el-table
            :data="tableData.filter(data => !search || data.stuName.toLowerCase().includes(search.toLowerCase()))"
            style="width: 1240px">
            
            <el-table-column
            prop="id"
            label="ID"
            width="80"
            >
            </el-table-column>

            <el-table-column
            prop="stuName"
            label=""
            width="130"
            >
            </el-table-column>

            <el-table-column
            label=""
            prop="stuCode"
            width="127">
            </el-table-column>

            <el-table-column
            label=""
            prop="lateTime"
            width="140">
            </el-table-column>

            <el-table-column
            label=""
            prop="createTime"
            width="200">
            </el-table-column>

            <el-table-column
            label=""
            prop="updateTime"
            width="180">
            </el-table-column>

            <el-table-column
            align="right"
            label="">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="primary"
                    @click="handleEdit(scope.row)"></el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row)"></el-button>
                    <el-button
                    size="mini"
                    type="info"
                    @click="handleShowTableTime(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>


            <el-pagination
            v-if="search === ''"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page.sync="currentPage"
            :page-size="7"
            layout="total, prev, pager, next, jumper"
            :total="total"
            >
            </el-pagination>

        
        <el-dialog title="" :visible.sync="dialogFormVisible4UpdateStu" width="800px" >

            <el-form :model="UpdateData" :rules="rules" ref="UpdateData" label-width="100px" class="demo-ruleForm">

                <span style="font-size:20px">ID:{{this.UpdateData.id}}</span>     

                <el-table
                :data="ShowStuTableInfData"
                style="width: 100%">

                    <el-table-column
                        prop="classToClass"
                        label="/"
                        width="110">
                    </el-table-column>

                    <el-table-column
                        prop="monday"
                        label=""
                        width="130">
                    </el-table-column>

                    <el-table-column
                        prop="tuesday"
                        label=""
                        width="130">
                    </el-table-column>

                    <el-table-column
                        prop="wednesday"
                        label=""
                        width="130">
                    </el-table-column>

                    <el-table-column
                        prop="thursday"
                        label=""
                        width="130">
                    </el-table-column>

                    <el-table-column
                        prop="friday"
                        label=""
                        width="130">
                    </el-table-column>

                </el-table> 

                <el-form-item style="margin-top:10px" prop="stuName" label="">
                    <el-input v-model="UpdateData.stuName" placeholder=""></el-input>
                </el-form-item>

                <el-form-item  prop="stuCode" label="">
                    <el-input v-model="UpdateData.stuCode" placeholder=""></el-input>
                </el-form-item>





                <el-button type="primary" @click="needChangeTimeTable" v-if="!this.needForm" style="margin-bottom:10px"></el-button>

                <div v-if="needForm">
                <el-form-item prop="dayWeek" label="">
                    <el-input v-model="UpdateData.dayWeek" placeholder=""/>
                </el-form-item>

                <el-form-item prop="classToClass" label="">
                    <el-input v-model="UpdateData.classToClass" placeholder=""/>
                </el-form-item>

                <el-form-item prop="startWeek" label="">
                    <el-input v-model="UpdateData.startWeek" placeholder=""/>
                </el-form-item>

                <el-form-item prop="endWeek" label="">
                    <el-input v-model="UpdateData.endWeek" placeholder=""/>
                </el-form-item>

                <el-form-item prop="isSingle" label="">
                    <el-input v-model="UpdateData.isSingle" placeholder=""/>
                </el-form-item>
                </div>

                


                <el-row>
                    <el-button type="primary" @click="Update('UpdateData')"></el-button>
                    <el-button type="primary" @click="close()"></el-button>
                </el-row>
            </el-form>

        

            <p style="color:red">,!!</p>
            <p>:.....</p>
            <p>:1-23-45-67-89-10</p>
            <p>:</p>      
        </el-dialog>

        <el-dialog title="" :visible.sync="dialogFormVisibleTableStu" width="1120px" >
            <el-table
            :data="ShowTimeTableData"
            style="width: 100%">

                <el-table-column
                    prop="stuName"
                    label=""
                    width="180">
                </el-table-column>

                <el-table-column
                    prop="dayWeek"
                    label=""
                    width="180">
                </el-table-column>

                <el-table-column
                    prop="classToClass"
                    label=""
                    width="180">
                </el-table-column>

                <el-table-column
                    prop="isSingle"
                    label=""
                    width="180">
                </el-table-column>

                <el-table-column
                    prop="startWeek"
                    label=""
                    width="180">
                </el-table-column>

                <el-table-column
                    prop="endWeek"
                    label=""
                    width="180">
                </el-table-column>

            </el-table>

        </el-dialog>


    </div>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            tableData: [],
            ShowTimeTableData:[],
            ShowStuTableInfData:[],
            search: "",
            total: 0,
            dialogFormVisible4UpdateStu: false,
            dialogFormVisibleTableStu:false,
            needForm:false,
            currentPage: 1,
            UpdateData:{},
            
            rules: {
                stuName: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 5, message: '24', trigger: 'blur' }
                ],
                stuCode:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 50, message: '350', trigger: 'blur' }
                ],
                dayWeek:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 2, message: '2', trigger: 'blur' }
                ],
                classToClass:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 4, message: '3-4', trigger: 'blur' }
                ],
                startWeek:[
                    { required: true, message: '', trigger: 'blur' },
                    { min:1, max: 2, message: '12', trigger: 'blur' }
                ],
                endWeek:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 1, max: 2, message: '12', trigger: 'blur' }
                ],
                isSingle:[
                    { required: true, message: '', trigger: 'blur' },
                    { min: 1, max: 1, message: '1', trigger: 'blur' }
                ]
            }
        }
    },

    mounted(){
        this.showStuList()
    },

    methods:{
        //
        getInf(stuCode){
            axios.get("/stuInf/getOne/"+stuCode).then((res)=>{
                this.UpdateData = res.data.data
            })
        },  

        getTableInf(stuCode){
            axios.get("/stuInf/getTable/"+stuCode).then((res)=>{
                this.ShowStuTableInfData = res.data.data
            })
        },

        //
        handleEdit(row) {
            this.needForm = false   
            this.getInf(row.stuCode)
            this.getTableInf(row.stuCode)
            this.dialogFormVisible4UpdateStu = true

        },

        //
        handleDelete(row) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                axios.delete("/stuInf/delStu/"+row.id).then((res)=>{
                    if(res.data.code === 202371){
                        this.$message.success(res.data.data) 
                        this.handleCurrentChange(this.currentPage)
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            })

        },

        //
        showStuList(){
            axios.get("/stuInf/1").then((res)=>{
                if(res.data.code === 202391){
                    this.tableData = res.data.data.records
                    this.total = res.data.data.total
                    this.currentPage = 1
                }else{
                    this.$message.error("!")
                }
            })
        },

        //
        handleCurrentChange(val){
            axios.get("/stuInf/"+val).then((res)=>{
                if(res.data.code === 202391){
                    this.tableData = res.data.data.records
                    this.total = res.data.data.total
                    this.currentPage = val
                }else{
                    this.$message.error("!")
                }

            })
        },

        handleSizeChange(val){
            

        },
        
        Update(UpdateData){
            this.$refs[UpdateData].validate((valid) => {
                if (valid) {
                    console.log(this.UpdateData);
                    axios.put("/stuInf/updateStuInf",this.UpdateData).then((res)=>{
                        if(res.data.code === 202381){
                            this.$message.success(res.data.data)
                            this.dialogFormVisible4UpdateStu = false
                            this.handleCurrentChange(this.currentPage)
                        }else if(res.data.code === 202380){
                            this.$message.error(res.data.data)
                        }else{
                            this.$message.warning(res.data.msg)
                        }
                    })
                }
            })
        },
        //
        handleShowTableTime(row){
            this.dialogFormVisibleTableStu = true
            axios.get("/stuInf/getOneTableTime/"+row.id).then((res)=>{
                if(res.data.code === 202391){
                    this.ShowTimeTableData = res.data.data
                }else{
                    this.$message.warning("")
                }
            })
        },

        needChangeTimeTable(){
            this.needForm = true
        },

        close(){
            this.dialogFormVisible4UpdateStu = false
        }

    }
}
</script>

<style>
    .timeTable{
        border-top: none;
        border-left: none;
        border-right: none;
    }
</style>